s<template>
  <div>
    <AppHeader>排行榜</AppHeader>
    <div>
      <ul class="rank-box">
        <li v-for="(item,index) in songlist" :key="index" class="rankitem" >
          <div class="left-box">
            <img :src="item.img" alt />
            <p>{{item.name}}</p>
            <!-- 播放量 -->
            <p class="playcount">
              <i class="el-icon-caret-right"></i>
              {{item.playCount|number}}
            </p>
          </div>
          <div class="right-box">
              <div v-for="(i,d) in item.list.slice(0,3)" :key="d" class="info">
                  <p>{{(d+1)+'.'+i.al.name}}</p>
                 
              </div>
          </div>
          <!--  -->
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import AppHeader from "../user/component/AppHeader.vue";
export default {
     filters:{
        // 格式播放量
    number(count){
        if(count>1000){
           return (count/10000).toFixed(2)+'万'
        }else{
           return count
        }
    }
    },
  components: {
    AppHeader,
  },
  data() {
    return {
      songrank: [],
      songlist:[],  
    };
  },
  created() {
    this.getRank();
    
  },
  methods: {
    async getRank() {
      const { data: res } = await this.$http.get("/toplist");
      this.songrank = res.list;
      
    //   console.log(res.list);   
        this.getRankSong()
    },
   async getRankSong(){
      
       for (let i=0;i<this.songrank.length;i++){  
           const {data:res}= await this.$http.get('/playlist/detail',{params:{id:this.songrank[i].id}})
           
            let rank={}
            rank.id=this.songrank[i].id
            rank.list= res.playlist.tracks  
           rank.img=res.playlist.coverImgUrl 
           rank.name=res.playlist.name 
           rank.playCount=this.songrank[i].playCount
            this.songlist.push(rank)   
           
       }
        // console.log(this.songlist)

    }
  },
};
</script>


<style lang='less' scoped>
.rank-box {
  width: 95%;
  margin-left: 5%;
  margin-top: 20px;
 
}
.rankitem {
  width: 100%;
  height: 150px;
  display: flex;
  .left-box{
      position: relative;
      width:100px;
      height: 100px;
      font-size: 14px;
      float: left;
      img {
    width: 100px;
    height: 100px;
  }
  .playcount {
        position: absolute;
        top: 0;
        right: 0;
        color: #fff;
        font-size: 10px;
        img {
          width: 10px;
          height: 10px;
        }
      }
}
  }
  .right-box{
     margin-left: 10px;
      .info{
          p{
              display: inline-block;
              font-size: 14px;
              line-height: 30px;
          }
          :last-child{
              margin-left: 5px;
          }
          
      }
  }
  
</style>